import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';
import { FormGroup, FormBuilder } from '@angular/forms';
import { LoaderProvider } from '../../providers/loader';
import { Trucks } from '../../providers/truck';
import { ToastProvider } from '../../providers/toast';
import { HomePage } from '../home/home';

/**
 * Generated class for the EditCompanyRequirementsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-edit-company-requirements',
  templateUrl: 'edit-company-requirements.html',
})
export class EditCompanyRequirementsPage {

  editReqForm: FormGroup;
  addReqForm: FormGroup;
  reqDetails;
  getIndex;
  companyListData;
  type;

  types = [
    {name: 'OPEN', value: 'OPEN'},
    {name: 'CONTAINER', value: 'CONTAINER'},
    {name: 'TRAILER', value: 'TRAILER'},
  ];

  capacity = [];


  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public fb: FormBuilder,
    public viewCtrl: ViewController,
    public loader: LoaderProvider,
    public trucks: Trucks,
    public toast: ToastProvider
    ) {

    this.reqDetails = this.navParams.get('data');
    this.getIndex = this.navParams.get('index');
    this.companyListData = this.navParams.get('companyListData');
    this.type = this.navParams.get('type');

    console.log(">>>Type", this.type);

    if(this.type === 'add') {
      this.addReqForm = this.fb.group({
        material: [''],
        to_destination: [''],
        vehicle_capacity: [''],
        vehicle_type: ['']
      });
    } else {
      this.editReqForm = this.fb.group({
        material: [this.reqDetails.material],
        to_destination: [this.reqDetails.to_destination],
        vehicle_capacity: [this.reqDetails.vehicle_capacity],
        vehicle_type: [this.reqDetails.vehicle_type]
      });  
    }
  }

  getCapacity(){

    if(this.type === 'add') {
      var ttype = this.addReqForm.value.vehicle_type;
    } else {
      var ttype = this.editReqForm.value.vehicle_type;
    }
    this.loader.show();
    this.trucks.getCapacityOfTruck().subscribe((res: any) => {
      this.loader.hide();
      var data = res.json(); 
      
      this.capacity = [];
      data.results.forEach(r => {
        // console.log('RR >>>>>>> ', r);
        if (r.type == ttype) {
          
          // console.log('>>>>>>> ', r);
          r.capacity.forEach( a => {
            var obj = {
              "name": a.ton,
              "value": a.ton,
            }
            this.capacity.push(obj);

            if(this.type === 'add') {
              this.addReqForm.controls['vehicle_capacity'].setValue(r.capacity[0].ton);
            } else {
              this.editReqForm.controls['vehicle_capacity'].setValue(r.capacity[0].ton);
            }
          });
        }
      });
    }, err => {
      this.loader.hide();
      console.log('>>>>>>>error', err);
    });

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad EditCompanyRequirementsPage');
  }

  update(isValid) {
    if(isValid) {
      this.viewCtrl.dismiss({requirementFormData: this.editReqForm.value, index: this.getIndex, listData: this.companyListData});
    }
  }

  Home(){
    this.navCtrl.setRoot(HomePage);
  }

  add() {
    this.loader.show();
    this.viewCtrl.dismiss({requirementFormData: this.addReqForm.value});
    this.toast.show({ 'message': 'Added successfully.' });
    this.loader.hide();
  }

  dismiss() {
    this.viewCtrl.dismiss();
  }

}



